import React, {Component} from 'react';
import $ from 'jquery';
import {Link} from 'react-router-dom'
class Registration extends Component{
  next = () => {
    if(!/^[A-Za-z].{5,}$/.test(this.refs.account.value)){
      alert('');
      return
    }
    let accountArr = [];
    this.props.data.user.forEach((e,i)=>{
      accountArr.push(e.account);
    })
    if( accountArr.includes(this.refs.account.value) ){
      alert('')
      return
    }
    if(!/^\d{6,}$/.test(this.refs.password.value)){
      alert('');
      return
    }
    if(this.refs.confirm.value !== this.refs.password.value){
      alert('');
      return
    }
    this.props.registrationHalfT(this.refs.account.value,this.refs.password.value);
  }
  blur = () => {
    this.props.data.user.forEach((e,i)=>{
      if( this.refs.account.value === e.account ){
        alert('')
        return
      }
    })
  }
  render(){
    console.log(this.props.data.user)
    return (
      <div className="registration">
        <h3 className="registration_welcome"></h3>
        <input type="text" className="regloginput" placeholder="" ref="account" onBlur={this.blur}/>
        <input type="password" className="regloginput" placeholder="" ref="password"/>
        <input type="password" className="regloginput regloginputBorder" placeholder="" ref="confirm"/>
        <input type="button" className="regloginputBtn" value="" onClick={this.next}/>
        <p className="reglogP"><a style={{textDecoration:'underline'}} href="https://accounts.douban.com/app/agreement"></a></p>
        <Link to="/login"><p className="reglogPCb"></p></Link>
      </div>
    )
  }
}
export default Registration



