import React, {Component} from 'react';
import $ from 'jquery';
import {Link,Redirect} from 'react-router-dom';
import dataLogin from './data/dataLogin.js';

class Login extends Component{
  landing = () => {
    if(this.props.data.user.filter(e=>e.account === this.refs.account.value).length){
      this.props.data.user.forEach((e,i)=>{
        if(e.account === this.refs.account.value){
          if(e.password === this.refs.password.value){
            this.props.landfallBol(e);
          }else{
            alert('')
            return
          }
        }
      })
    }else{
      alert('');
    }
  }
  componentDidMount(){
    this.props.registrationHalfF();
    this.props.registrationFinishF();
  }
  render(){
    console.log(this.props.data)
    return (
      <div>
        <div className="login">
    			<h3 className="login_title">
    				<Link to="/"><span className="login_cancel"></span></Link>
    				<span></span>
    			</h3>
    		</div>
    		<div className="login_accounts">
    			<input type="text" className="login_input" placeholder="" ref="account"/>
    			<input type="password" className="login_input" placeholder="" ref="password"/>
    			<input type="button" className="login_inputBtn" value="" onClick={this.landing}/>
    			<Link to="/registration"><p className="login_inputP"></p></Link>
    		</div>
      </div>
    )
  }
}
export default Login



